import {
	mapState,
	mapActions
} from 'vuex'
import commongood from '../../components/common/commongood.vue'
export default {
	name: "",
	components: {
		commongood
	},
	props: {},
	data() {
		return {
			// 接收传过来的id
			brandId: null
		}
	},
	methods: {
		...mapActions(['getBrands']),
	},
	mounted() {
		if (this.brandId !== null) {
			this.getBrands(this.brandId)
		}
	},
	onLoad(option) {
		this.brandId = option.id
	},
	filters: {

	},
	computed: {
		...mapState(['brandObj'])
	},
	watch: {

	},
	directives: {

	}
}
